  position: absolute;
  width: 30px;
  height: 30px;
  top: -5px;
  left: -5px;
}
.vux-header .vux-header-left .left-arrow:before {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  border: 1px solid #ccc;
  border-width: 1px 0 0 1px;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
  top: 8px;
  left: 7px;
}
